:root {
  --font-family: "DM Sans", sans-serif;
  --second-family: "Titan One", sans-serif;
  --background-color: #fff;
  --header-background: #ffb8ca;
  --dot-color: #c7133e;
  --inactive-dot-color: #d9d9d9;
  --container-background: #fafafa;
  --section-background: #fff5f6;
  --button-hover-color: #ecebeb;
  --red-text-color: #d41443;
  --mobile-header-bkg: #e17992;
  --mobile-nav-border: #e18298;
  --h3-title-color: #ffa5ba;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

h1 {
  font-family: var(--second-family);
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--background-color);
  font-size: 38px;
  font-weight: 400;
}

.header {
  background-color: var(--header-background);
  max-width: 75rem;
  margin: 0 auto;
  padding: 2.5rem 1rem 0;
  overflow: hidden;
}

.container-row {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  z-index: 1;
  position: relative;
}

.main-navigation ul {
  z-index: 1;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 2.188rem;
  padding-left: 0;
  display: flex;
  position: relative;
}

.main-navigation a {
  color: #fff;
  font-size: 16px;
  font-weight: 700;
}

nav > ul > li:first-child > a {
  color: var(--red-text-color);
}

.buy-now-button {
  background: var(--background-color);
  font-family: var(--font-family);
  color: var(--red-text-color);
  cursor: pointer;
  border: none;
  border-radius: 1.375rem;
  outline: none;
  justify-content: center;
  align-items: center;
  max-width: 13.063rem;
  font-weight: 700;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.buy-now-button:hover {
  background: var(--button-hover-color);
}

.js-menu-container, .burger-menu-wrapper {
  cursor: pointer;
  display: none;
}

.burger-button-wrapper {
  align-items: center;
  gap: 2rem;
  display: flex;
}

.js-menu-container {
  display: none;
}

.js-menu-container.is-open {
  z-index: 2;
  background-color: var(--mobile-header-bkg);
  flex-flow: column wrap;
  gap: 1.75rem;
  min-width: 15.875rem;
  height: 100%;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
  box-shadow: -8px 0 20px #00000040;
}

.close-btn {
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  justify-content: flex-end;
  display: flex;
  position: absolute;
  top: 1.25rem;
  right: 2rem;
}

.mobile-menu {
  flex-direction: column;
  gap: 2rem;
  padding-top: 3.625rem;
  display: flex;
}

.mobile-menu > .main-navigation ul {
  flex-direction: column;
  gap: 0;
  margin: 0;
  padding: 0;
  display: flex;
}

.mobile-menu > .main-navigation li {
  border-bottom: 1px solid var(--mobile-nav-border);
  padding: 13px 20px 11px;
}

.js-menu-container nav > ul > li:first-child {
  border-top: 1px solid var(--mobile-nav-border);
}

@media screen and (width <= 965px) {
  .mobile-header {
    display: flex;
  }

  header > div.container-row > nav {
    display: none;
  }

  .burger-menu-wrapper {
    display: flex;
  }

  .header {
    padding: 2rem 2rem 0;
  }

  .mobile-menu .buy-now-button {
    margin: 0 2.125rem;
  }
}

@media screen and (width >= 481px) {
  .container-row .buy-now-button {
    min-width: 10rem;
  }
}

@media screen and (width <= 480px) {
  div.container-row > div.burger-button-wrapper > button {
    display: none;
  }

  .js-menu-container {
    min-width: 15.625rem;
  }

  .mobile-menu .buy-now-button {
    margin: 0 1.25rem;
  }

  .header {
    padding: .5rem 1.25rem 0;
  }

  .hero-middle-container {
    width: 345px;
    height: 345px;
    right: -81px;
  }
}

.hero-section {
  background-color: var(--header-background);
  justify-content: space-between;
  max-width: 75rem;
  height: 572px;
  margin: 0 auto;
  display: flex;
  position: relative;
}

.hero-section img {
  max-width: 100%;
  height: auto;
}

.hero-left-container {
  flex-direction: column;
  max-width: 50%;
  display: flex;
}

.hero-title-holder {
  z-index: 1;
  max-width: 17%;
  padding-top: 45px;
  display: flex;
  position: absolute;
}

.hero-section span {
  color: var(--red-text-color);
  font-size: 58px;
  line-height: .75;
}

.hero-button-holder {
  z-index: 1;
  gap: 12px;
  display: flex;
  position: absolute;
  top: 40%;
}

.hero-button {
  background-color: var(--red-text-color);
  border: none;
  border-radius: 20px;
  outline: none;
  padding: 10.2px 24.3px;
}

.hero-button-holder > button:nth-child(2) {
  background-color: var(--background-color);
  color: var(--red-text-color);
  padding: 10.2px 20.15px;
}

.hero-button-holder > button:nth-child(2):hover {
  background: var(--button-hover-color);
}

.hero-text {
  font-family: var(--font-family);
  color: var(--background-color);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.4;
}

.corner-image-text-holder {
  flex-shrink: 0;
  display: flex;
  position: absolute;
  bottom: 60px;
  left: 0;
}

.corner-image-text-holder img {
  width: 213px;
}

.text-button-holder p.hero-text {
  max-width: 200px;
  margin: 0;
  padding-top: 25px;
  display: flex;
}

.text-button-holder {
  z-index: 1;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  display: flex;
  position: relative;
}

.hero-round-button {
  background: var(--background-color);
  fill: var(--h2-title-color);
  border: 0;
  border-radius: 50%;
  outline: 0;
  justify-content: center;
  align-items: center;
