}

.hero-middle-container {
  background-color: var(--h3-title-color);
  z-index: 0;
  border-radius: 50%;
  width: 534px;
  height: 534px;
  display: flex;
  position: absolute;
  top: -35px;
  right: 133px;
}

.hero-image img {
  width: 236px;
  position: absolute;
  top: 50px;
  right: 179px;
}

.hero-right-container {
  flex-direction: column;
  max-width: 50%;
  display: flex;
}

.cafes, .trucks {
  background-color: var(--background-color);
  border-radius: 50%;
  width: 30px;
  height: 30px;
}

.cafes-holder {
  position: relative;
  top: 66px;
}

.cafes {
  position: absolute;
  top: 0;
  right: 0;
}

.cafes h4, .trucks h4 {
