}

.trucks-holder {
  position: relative;
  top: 155px;
  right: 0;
}

.trucks {
  position: absolute;
  top: 0;
  right: 0;
}

.right-corner-image-holder {
  display: flex;
  position: absolute;
  bottom: -3px;
  right: 0;
}

.right-corner-image-holder img {
  width: 304px;
}

@media screen and (width <= 960px) {
